import {
	ProductRequest
} from "../../models/product.js"
const productRequest = new ProductRequest()
export default {
	data() {
		return {
			product: {}
		}
	},
	onLoad(e) {
		this.readProduct(e.id)
	},
	methods: {
		readProduct(id) {
			uni.showLoading({
				title: '加载中'
			});
			productRequest.read({
				where: {
					and: {
						product_id: id
					}
				}
			}).then(r => {
				this.product = r.data[0];
				uni.hideLoading()
			}).catch(e => {
				uni.hideLoading()
			})
		},
		handleText(text) {
			if (!text)
				return
			let reg = new RegExp('\\\\n', 'g')
			return text.replace(reg, '\n')
		},
		createOrder(e) {
			uni.navigateTo({
				url: '../order/order?orderid=' + Date.now() + '&pid=' + this.product.id
			})
		}
	}
}
